import React from 'react';
import imageCompression from "browser-image-compression";
import BaseIndexPage from "components/HOC/BaseIndexPage";
import { PROPERTY } from "components/Routes/Constants";
import Modal from "components/global-components/Components/Modal";
import ConfirmationFlow from "components/global-components/Components/SweetAlert/ConfirmationFlow";
import isEmpty from "lodash/isEmpty";
import isNil from "lodash/isNil";
import map from "lodash/map";
import size from "lodash/size";
import { useState } from "react";
import { connect } from "react-redux";
import { Route, Switch } from "react-router-dom";
import { Button, Col, Container, Row } from "reactstrap";
import {
  updateImages as UpdateImages,
  create,
  get,
  setFormEmpty,
  update,
} from "state/Listing/Epic";
import { selectItem } from "state/Listing/Selectors";
import uploadToS3 from "../../lib/uploadToS3";
import Congratulation from "./Components/CreateListing/Components/Congratulation";
import CreateList from "./Components/CreateListing/index";
import {
  PublishModalBody,
  PublishModalHeader,
} from "./Components/PublishModal/index";

let style = {
  minWidth: "35%",
};
const AddListing = ({ item, onSubmitItem, UpdateImages, ...props }) => {
  const {
    match: { path },
  } = props;
  const [isModalVisisble, setisModalVisisble] = useState(false);
  const [isCongVisible, setisCongVisible] = useState(false);
  const [listingImages, setListingImages] = useState(false);
  const [isShowConfirmation, setIsShowConfirmation] = useState(false);
  const [isConfirmed, setisConfirmed] = useState(false);
  const [isPublishing, setisPublishing] = useState(false);
  const [formValues, setformValues] = useState("");
  const [isImageUploading, setIsImageUploading] = useState(false);

  console.log("props.isSubmitting", props.isSubmitting);

  const toggleModal = () => {
    setisModalVisisble(!isModalVisisble);
  };
  const toggleCongratulation = () => {
    setisCongVisible(!isCongVisible);
  };
  // const setInitialValues = () => {
  //   if (isEmpty(item)) {
  //     return initialValues;
  //   } else {
  //     const { display_id, ...rest } = item;
  //     return { id: display_id, display_id, ...rest, ...listingImages };
  //   }
  // };

  const getBase64Images = (imageList) => {
    return map(imageList, ({ preview, location }) => {
      return { location: !isNil(location) ? location : preview };
    });
  };
  const handleImageUpload = async (files) => {
    const handleCompression = async (files, index) => {
      var options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      if (index < size(files)) {
        if (isNil(files[index].location)) {
          let name = files[index].name;
          let imageType = files[index].type;
          let imagePath = files[index].path;
          let blobImages = await imageCompression(files[index], options);
          files[index] = new File([blobImages], name, {
            lastModified: new Date().getTime(),
            type: imageType,
            path: imagePath,
          });
        }

        handleCompression(files, index + 1);
      } else {
        files = files;
      }
    };

    return files;
  };

  const getFormattedImageURL = ({ images_list, type, display_id }) => {
    let { REACT_APP_S3_BUCKET_URL, REACT_APP_IS_PROD } = process.env;

    return map(images_list, ({ name, ...rest }, index) => {
      
      if (!isNil(rest.preview)) {
        let fileName = name;
        let sub_location = `${REACT_APP_IS_PROD}/listings/${display_id}_${type}_${
          index + 1
        }${fileName.substring(fileName.lastIndexOf("."))}`;
        return {
          location: `${REACT_APP_S3_BUCKET_URL}/${sub_location}`,
          sub_location,
          type,
          name,
          fileName,
        };
      }

      return {
        name,
        ...rest,
      };
    });
  };

  const confirmThenSubmit = async () => {
    setIsShowConfirmation(true);
    let {
      amenities,
      floor_plan,
      image_attributes,
      ...restListing
    } = formValues;

    let floorImage = getBase64Images(floor_plan);

    let ImgAttribute = getBase64Images(image_attributes);

    setListingImages({
      floor_plan: floorImage,
      image_attributes: ImgAttribute,
    });
    if (!isEmpty(item)) {
      const { _id: id, display_id } = item;

      let floorImages = getFormattedImageURL({
        display_id,
        images_list: floor_plan,
        type: "floor_plan",
      });
      let imageAttributes = getFormattedImageURL({
        display_id,
        images_list: image_attributes,
        type: "image_attributes",
      });

      restListing = {
        ...restListing,
        id: display_id,
        image_attributes: imageAttributes,
        floor_plan: floorImages,
        display_id,
      };
    } else {
      let display_id = Math.floor(Date.now() / 1000);
      // let floorImages = map(floor_plan, ({ name, ...rest }, index) => {
      //   // let fileName = rest.src.file.name;
      //   let fileName = name;
      //   let sub_location = `${REACT_APP_IS_PROD}/listings/${display_id}_floorPlan_${
      //     index + 1
      //   }${fileName.substring(fileName.lastIndexOf("."))}`;
      //   return {
      //     location: `${REACT_APP_S3_BUCKET_URL}/${sub_location}`,
      //     sub_location,
      //     type: "floor_plan",
      //     name: name,
      //     fileName,
      //   };
      // });

      let floorImages = getFormattedImageURL({
        display_id,
        images_list: floor_plan,
        type: "floor_plan",
      });

      let imageAttributes = getFormattedImageURL({
        display_id,
        images_list: image_attributes,
        type: "image_attributes",
      });

      // map(image_attributes, ({ name, ...rest }, index) => {
      //   // let fileName = rest.src.file.name;
      //   let fileName = name;
      //   let sub_location = `${REACT_APP_IS_PROD}/listings/${display_id}_image_gallery_${
      //     index + 1
      //   }${fileName.substring(fileName.lastIndexOf("."))}`;
      //   return {
      //     location: `${REACT_APP_S3_BUCKET_URL}/${sub_location}`,
      //     sub_location,
      //     type: "image_attributes",
      //     name: name,
      //     fileName,
      //   };
      // });

      restListing = {
        ...restListing,
        display_id,
        floor_plan: floorImages,
        image_attributes: imageAttributes,
      };
    }

    // if (!isNil(amenities) && size(amenities) > 0) {
    //   amenities = first(amenities);
    //   amenities = {
    //     objectToFilter: amenities,
    //     valueToFilter: true,
    //   };

    //   amenities = filterObjectByValue(amenities);
    //   amenities = [amenities];
    // }

    onSubmitItem({ ...restListing, amenities }, async ({ data }) => {
      const { error = "" } = data;

      if (isEmpty(error)) {
        window.scrollTo(0, 0);
        setisConfirmed(true);
        setIsImageUploading(true);
        props.turnOnLoading();
        const { _id } = data;
        let floorImage = await handleImageUpload(floor_plan);

        let ImgAttribute = await handleImageUpload(image_attributes);

        const Images = {
          entity_type: "listings",
          file_first_name: `${data.display_id}_`,
          entity_id: _id,
          floor_plan_url: data.floor_plan,
          image_attributes_url: data.image_attributes,
          floor_plan: floorImage,
          image_attributes: ImgAttribute,
        };

        await uploadToS3({ fileArray: Images }, () => {
          setIsImageUploading(false);
          props.turnOffLoading();
          let hash = props.history.location.hash;

          window.location = `${PROPERTY}/${data.display_id}#step${
            hash === "#step2" ? "3" : "2"
          }`;
        });
        // UpdateImages(_id, Images)
        //   .then((res) => {})
        //   .catch((err) => {});
      }
    });
  };

  const onConfirmClosed = () => {
    setIsShowConfirmation(false);
    setisPublishing(false);
    setisConfirmed(false);
  };

  const handleSubmit = (values, { rest }) => {
    let hash = props.history.location.hash;

    setformValues(values);
    if (hash === "#step2") {
      setisPublishing(true);
    } else if (hash === "#step1") {
      setIsShowConfirmation(true);
    }
  };

  const onChangePublish = () => {
    console.log(`Is Publishing`, formValues);
    const { history } = props;
    const { is_published, id } = formValues;

    onSubmitItem({ is_published, id }, async ({ data }) => {
      const { error = "" } = data;

      if (isEmpty(error)) {
        setisConfirmed(true);
        if (is_published) {
          window.location = `${PROPERTY}/${id}#step3`;
          toggleModal();
        }
      }
    });
  };

  return (
    <Switch>
      <Route path={`${path}/:id`}>
        <div className="add-new-property-area pt-4 hide-overflow">
          <Container>
            <Row>
              <Col xl="12" lg="12">
                {!props.isLoading && (
                  <div className="section-title text-center">
                    <h3>
                      {props.getIdMode({ ...props }) ? `Update` : `Add`} New
                      Property
                    </h3>
                  </div>
                )}

                {isCongVisible && <Congratulation />}

                <CreateList
                  item={{ ...item, ...listingImages }}
                  listingImages={listingImages}
                  isImageUploading={isImageUploading}
                  // initialValues={setInitialValues()}
                  {...props}
                  toggleCongratulation={toggleCongratulation}
                  toggleModal={toggleModal}
                  onSubmit={handleSubmit}
                />
                {isModalVisisble && (
                  <Modal
                    onSubmit={() => setisModalVisisble(false)}
                    isModalVisible={isModalVisisble}
                    toggle={() => toggleModal()}
                    header={<PublishModalHeader />}
                    style={style}
                  >
                    <PublishModalBody
                      onRequestVerification={() => setisModalVisisble(false)}
                    />
                  </Modal>
                )}
              </Col>
              <ConfirmationFlow
                show={isShowConfirmation}
                isConfirmed={isConfirmed}
                onConfirm={() => confirmThenSubmit()}
                onClose={() => onConfirmClosed()}
                onCancel={() => {
                  setIsShowConfirmation(false);

                  props.turnOffSubmitting();
                }}
                confirmButtonColor="#28a745"
                confirmButtonText="Save"
                title="Are you sure?"
                type="info"
                text="You want to save changes?"
              />
              {!isEmpty(item) && (
                <ConfirmationFlow
                  show={isPublishing}
                  isConfirmed={isConfirmed}
                  onConfirm={() => {
                    onChangePublish();
                  }}
                  onClose={() => onConfirmClosed()}
                  onCancel={() => {
                    setisPublishing(false);
                    props.turnOffSubmitting();
                  }}
                  confirmButtonText={
                    item.is_published ? "UnPublish" : "Publish"
                  }
                  title="Are you sure?"
                  type={item.is_published ? "warning" : "info"}
                  text={`You want to ${
                    item.is_published ? "UnPublish" : "Publish"
                  } this listing?`}
                  confirmButtonColor={item.is_published ? "#dc3545" : "#28a745"}
                />
              )}
            </Row>
          </Container>
        </div>
      </Route>
    </Switch>
  );
};

const mapStateToProps = (state) => {
  return {
    Header: {
      headertitle: "Add Property",
      subheader: "Add your listing to DirectRent",
    },
    item: selectItem(state),
    hideSearch: true,
    pageName: "AddProperty",
    blockingMessage: "Please wait we are setting up your listings!",
    footer: {
      isShowUpdate: false,
      title: (
        <>
          <i
            className="far fa-user"
            style={{
              background: "#ebeef3",
              padding: "10px",
              borderradius: "50%",
            }}
          ></i>
          Refer Landloard
        </>
      ),

      isRefer: (
        <Button
          to="/invite"
          className="btn btn-success "
          style={{ position: "relative" }}
        >
          Refer
        </Button>
      ),
    },
  };
};

const mapDispatchToProps = (dispatch) => ({
  create: (params) => dispatch(create(params)),
  update: (id, params) => dispatch(update(id, params)),
  get: (params) => dispatch(get(params)),

  UpdateImages: (id, params) => dispatch(UpdateImages(id, params)),
  setFormEmpty: () => dispatch(setFormEmpty()),
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseIndexPage(AddListing));
