import React from "react";
import isEmpty from "lodash/isEmpty";
import { Row } from "reactstrap";

const Nav = ({ onStepClick, item, ...props }) => {
  const dots = [];
  var iconsArray = [
    { text: "Add Property Details", icon: "fas fa-home" },
    { text: "Preview Listing", icon: "fas fa-tasks" },
    { text: "Publish Listing", icon: "fas fa-upload" },
  ];
  for (let i = 1; i <= props.totalSteps; i += 1) {
    const isActive = props.currentStep === i;
    dots.push(
      <div className="col-4 " key={`step-${i}`}>
        <div className="single-intro style-two text-center">
          <div className={isActive ? `thumb` : `bgGray`}>
            <div
              onClick={() => {
                if (!isEmpty(item) && !isEmpty(item.display_id)) {
                  props.goToStep(i);
                  onStepClick(i);
                }
              }}
            >
              <i className={iconsArray[i - 1].icon}></i>
            </div>
          </div>
          <div className="details">
            <h5 className="title">{iconsArray[i - 1].text}</h5>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="border-bottom mb-4">
      <Row className="justify-content-around">{dots}</Row>
    </div>
  );
};

export default Nav;
