import { FormikHOC } from "components/global-components/Components/ReactStrapFormik/";
import React from "react";
import { connect } from "react-redux";
import StepWizard from "react-step-wizard";
import Nav from "./Nav";
import { ValidationSchema } from "../Form/validationSchema";
import AddressDetailsForm from "../Tabs/AddressDetailsForm";
import HouseDetailsForm from "../Tabs/HouseDetailsForm";
import LeaseDetailsForm from "../Tabs/LeaseDetailsForm";

const CreateList = ({
  toggleModal,
  toggleCongratulation,
  isEditMode = false,
  ...props
}) => {
  return (
    <StepWizard
      isHashEnabled
      nav={
        <Nav onStepClick={(index) => index === 3 && toggleModal()} {...props} />
      }
    >
      <AddressDetailsForm {...props} />
      {isEditMode && <HouseDetailsForm {...props} />}
      {isEditMode && (
        <LeaseDetailsForm
          {...props}
          toggleModal={toggleModal}
          onPackageSelect={(i) => toggleCongratulation(true)}
        />
      )}
    </StepWizard>
  );
};

const mapStateToProps = (state, props) => ({
  validationSchema: ValidationSchema,
  submitButtonLabel: "Save ",
  isFormikSubmit: false,
  validateOnChange: true,
});
const mapDispatchToProps = (dispatch) => ({});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FormikHOC(CreateList));
