import AWS from "aws-sdk";
import size from "lodash/size";
import isNil from "lodash/isNil";

const {
  REACT_APP_S3_BUCKET_NAME,
  REACT_APP_S3_BUCKET_REGION,
  REACT_APP_S3_IDENTITY_POOL_ID,
} = process.env;

AWS.config.update({
  region: REACT_APP_S3_BUCKET_REGION,
  credentials: new AWS.CognitoIdentityCredentials({
    IdentityPoolId: REACT_APP_S3_IDENTITY_POOL_ID,
  }),
});

var s3 = new AWS.S3({
  apiVersion: "2006-03-01",
  params: { Bucket: REACT_APP_S3_BUCKET_NAME },
});

const uploadToS3 = async ({ fileArray }, callback) => {
  console.log(fileArray);
  let promiseArray = [];

  await iterateThroughFiles(
    { fileArray, type: "floor_plan", url_type: "floor_plan_url" },
    0,
    promiseArray
  );
  await iterateThroughFiles(
    { fileArray, type: "image_attributes", url_type: "image_attributes_url" },
    0,
    promiseArray,
    callback
  );
  // iterateThroughFiles({ fileArray, type: image_attributes }, 0);
};

const iterateThroughFiles = async (
  { fileArray, type, url_type },
  index,
  promiseArray,
  callback
) => {
  if (size(fileArray[type]) > index) {
    let file = fileArray[type][index];

    if (!isNil(file.size) || !isNil(file.preview)) {
      const { sub_location: filePath } = fileArray[url_type][index];
      let onUploadComplete =
        size(fileArray[type]) === index + 1 ? callback : undefined;

      let promiseTemp = await finalUpload(
        { file, filePath },
        onUploadComplete,
        promiseArray
      );
      promiseArray = [...promiseArray, promiseTemp];
    }

    iterateThroughFiles(
      { fileArray, type, url_type },
      index + 1,
      promiseArray,
      callback
    );
  } else {
    if (!isNil(callback)) {
      Promise.all(promiseArray).then((values) => {
        callback();
      });
    }
  }
};

const finalUpload = async ({ filePath, file }, callback) => {
  return s3
    .upload({
      Key: filePath,
      bucketName: REACT_APP_S3_BUCKET_NAME,
      Body: file,
      ACL: "public-read",
    })
    .promise();
};

export default uploadToS3;
