import React from 'react';
import LoadingButton from "components/global-components/Components/Buttons/LoadingButton";

export default function Stats({
  currentStep,
  firstStep,
  goToStep,
  lastStep,
  nextStep,
  previousStep,
  totalSteps = 3,
  step,
  handleSubmit,
  isSubmitting,
  update,
  handleResponse,
  item = { is_published: false },
  toggleNavigation,
  setFieldValue,
  setFormEmpty,
  ...props
}) {
  console.log("State isSubmitting", isSubmitting);
  const finishStep = () => {
    props.history.push("/listing");
    // setFormEmpty();
    window.location = window.location.origin;
  };
  return (
    <div>
      <hr />

      {currentStep > 1 && (
        <button
          className="border btn btn-default btn-block mb-2"
          onClick={previousStep}
        >
          Go Back
        </button>
      )}
      {currentStep === 1 && (
        <LoadingButton
          label="Continue"
          btnclassName="btn-block"
          isLoading={isSubmitting}
          onClick={() => {
            handleSubmit();
          }}
        />
      )}
      {currentStep === 2 && (
        <LoadingButton
          label={`${item.is_published ? "Un Publish" : "Publish"}`}
          btnclassName="btn-block"
          // isLoading={isSubmitting}
          onClick={() => {
            setFieldValue("is_published", item.is_published ? false : true);
            setFieldValue("is_publish_changed", true);
            handleSubmit();
          }}
        />
      )}
      {currentStep === 3 && (
        <button
          className="btn btn-success btn-block"
          onClick={
            () => finishStep()

            // setFormEmpty(() => (window.location.href = window.location.origin));
            // setFormEmpty();
          }
        >
          Finish
        </button>
      )}
      {/* {currentStep < totalSteps ? (
        currentStep === 1 ? (
          
        ) : !isEmpty(item) && !isEmpty(item.display_id) ? (
          <>
           
          </>
        ) : (
          <button className="btn btn-blue btn-block" onClick={nextStep}>
            Continue
          </button>
        )
      ) : (
        
      )} */}
      <hr />
    </div>
  );
}
